@import url(https://fonts.googleapis.com/css2?family=Cabin&family=Francois+One&display=swap);
html,
body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  font-family: 'Francois One', sans-serif;
  font-size: 36px;
}

.App,
#root,
.react-swipeable-view-container {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}

#logo{
  position: absolute;
  top: 22px;
  left: 20px;
  z-index: 2;
}

.crossfade > figure {
  -webkit-animation: imageAnimation 30s linear infinite 0s;
          animation: imageAnimation 30s linear infinite 0s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  background-size: cover;
  background-position: center;
  color: transparent;
  height: 100%;
  left: 0px;
  opacity: 0;
  position: absolute;
  top: 0px;
  width: 100%;
  z-index: 0;
}

figure {
  margin: 0;
  padding: 0;
}

.crossfade > figure:nth-child(1) {
  background-image: url(/shopping-list/static/media/bg.9e26a529.jpg);
}

.crossfade > figure:nth-child(2) {
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
  background-image: url(/shopping-list/static/media/bg1.aa66c707.jpg);
}

.crossfade > figure:nth-child(3) {
  -webkit-animation-delay: 12s;
          animation-delay: 12s;
  background-image: url(/shopping-list/static/media/bg3.49636944.jpg);
}

.crossfade > figure:nth-child(4) {
  -webkit-animation-delay: 18s;
          animation-delay: 18s;
  background-image: url(/shopping-list/static/media/bg4.f17cf1a8.jpg);
}

.crossfade > figure:nth-child(5) {
  -webkit-animation-delay: 24s;
          animation-delay: 24s;
  background-image: url(/shopping-list/static/media/bg5.b255f789.jpg);
}

.help-btn {
  position: absolute;
  color: #fffb00;
  width: 24px;
  top: 25px;
  right: 25px;
  z-index: 100;
}

.item-view{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.navbar {
  color: whitesmoke;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #254e3d;
}

.navbar-icon {
  color: #254e3d;
  width: 50px;
  text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000,
    -1px 1px 0 #000, 1px 1px 0 #000;
}

.item-list,
.bought-items-list {
  background-color: #ffffff;
  box-shadow: 0px 0px 24px 2px rgba(0, 0, 0, 0.75);
  height: 65vh;
  color: #254e3d;
  padding: 20px;
  margin: 20px;
  opacity: 0.95;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Cabin', sans-serif;
  font-size: 20px;
}

.item-container {
  padding: 20px;
  margin-top: 5px;
  margin-bottom: 5px;
  height: 100%;
  overflow-y: auto;
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 45px;
  min-height: 45px;
  width: 250px;
  box-sizing: border-box;
  border-bottom: 1px solid #254e3d;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.trash {
  font-size: 20px;
}


.form {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

#add-field,
#submit-btn {
  height: 50px;
  box-sizing: border-box;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}

#add-field {
  background-color: #fbfffc;
  box-shadow: 0px 0px 64px 20px rgba(0, 0, 0, 0.75);
  border: none;
  padding: 20px;
  font-family: 'Cabin', sans-serif;
  border-radius: 40px 0 0 40px;
  width: 250px;
  text-align: center;
  color: #254e3d;
}

::-webkit-input-placeholder {
  color: #254e3d;
  opacity: 0.5
}

:-ms-input-placeholder {
  color: #254e3d;
  opacity: 0.5
}

::-ms-input-placeholder {
  color: #254e3d;
  opacity: 0.5
}

::placeholder {
  color: #254e3d;
  opacity: 0.5
}

#add-field:focus::-webkit-input-placeholder {
  opacity: 0.1;
}

#add-field:focus:-ms-input-placeholder {
  opacity: 0.1;
}

#add-field:focus::-ms-input-placeholder {
  opacity: 0.1;
}

#add-field:focus::placeholder {
  opacity: 0.1;
}

#submit-btn {
  padding: 20px;
  border-radius: 0 40px 40px 0;
  background: #254e3d;
  color: #fbfffc;
  border: none;
}

#add-icon {
  font-size: 40px;
  color: #254e3d;
}

@-webkit-keyframes imageAnimation {
  0% {
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    opacity: 0;
  }
  8% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    opacity: 1;
  }
  17% {
    opacity: 1;
  }
  25% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes imageAnimation {
  0% {
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    opacity: 0;
  }
  8% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    opacity: 1;
  }
  17% {
    opacity: 1;
  }
  25% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

